import Loader from 'components/Loader/Loader';
import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { fetchCredits } from 'Services/fetchApi';
import { ActorPhoto, CastItem, CastList, Description, Info } from './Cast.styled';

const Cast = () => {
  const [cast, setCast] = useState([]);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  const { movieId } = useParams();

  useEffect(() => {
    const fetchMovieCast = async () => {
      try {
        setLoading(true);
        const result = await fetchCredits(movieId);
        setCast(result);
      } catch (error) {
        setError('404 Page Not Found');
      } finally {
        setLoading(false);
      }
    };
    fetchMovieCast();
  }, [movieId]);
  return (
    <>
      {loading && <Loader />}
      {error && <div>{error}</div>}
      <CastList>
        {cast.map(({ id, name, character, profile_path }) => {
          return (
            <CastItem key={id}>
              <ActorPhoto
                src={`https://image.tmdb.org/t/p/w300${profile_path}` || "data:image/jpeg;base64,"}
                alt={`${name} portrait`}
              />
              <Info>
                <Description>Name: {name}</Description>
                <Description>Character: {character}</Description>
              </Info>
            </CastItem>
          );
        })}
      </CastList>
    </>
  );
};

export default Cast;
